* {
  box-sizing: border-box;
}
*:before,
*:after {
  box-sizing: border-box;
}
html {
  font-family: "Roboto", Helvetica, Arial, sans-serif;
  font-size: 16px;
  -webkit-tap-highlight-color: rgba(0,0,0,0);
}
body {
  line-height: 1.25;
  color: #fff;
  background-color: #111;
}
input,
button,
select,
textarea {
  font-family: inherit;
  font-size: inherit;
  line-height: inherit;
}
a {
  color: #e22d22;
  text-decoration: underline;
}
a:hover,
a:focus {
  color: #a21d15;
  text-decoration: underline;
}
a:active {
  color: #8c1912;
  text-decoration: underline;
}
figure {
  margin: 0;
}
img {
  vertical-align: middle;
}
.img-rounded {
  border-radius: 6px;
}
.img-thumbnail {
  padding: 4px;
  line-height: 1.25;
  background-color: #111;
  border: 1px solid #ddd;
  border-radius: 4px;
  transition: all 0.2s ease-in-out;
}
.img-circle {
  border-radius: 50%;
}
hr {
  margin-top: 20px;
  margin-bottom: 20px;
  border: 0;
  border-top: 1px solid #eee;
}
.sr-only {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  border: 0;
}
.sr-only-focusable:active,
.sr-only-focusable:focus {
  position: static;
  width: auto;
  height: auto;
  margin: 0;
  overflow: visible;
  clip: auto;
}
[role="button"] {
  cursor: pointer;
}
#root {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}
.wrapper {
  flex-grow: 1;
}
.loader {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #222;
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
  transition: opacity 250ms, visibility 250ms;
}
.loader .icon {
  position: relative;
  border-radius: 50%;
  padding: 10px;
  fill: #fff;
  animation: rotate 2s infinite;
  box-shadow: 0px 0px 10px 2px #fff;
}
.loader.visible {
  opacity: 1;
  visibility: visible;
  pointer-events: all;
}
@-moz-keyframes rotate {
  0% {
    transform: rotate(0deg);
  }
  50% {
    transform: rotate(180deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
@-webkit-keyframes rotate {
  0% {
    transform: rotate(0deg);
  }
  50% {
    transform: rotate(180deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
@-o-keyframes rotate {
  0% {
    transform: rotate(0deg);
  }
  50% {
    transform: rotate(180deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
@keyframes rotate {
  0% {
    transform: rotate(0deg);
  }
  50% {
    transform: rotate(180deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
.main-header {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  z-index: 1000;
  display: flex;
  border-right: 1px solid #e22d22;
  background-color: #000;
}
.main-header:hover .main-header__nav-item-text,
.main-header:focus .main-header__nav-item-text {
  max-width: 100px;
  max-height: 100px;
}
.main-header__nav {
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.main-header__nav-item {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 10px;
  color: #fff;
  text-decoration: none;
  font-size: 12px;
  text-transform: uppercase;
  letter-spacing: 0;
  transition: color 250ms;
}
.main-header__nav-item .icon {
  fill: #fff;
  transform: scale(1);
  transition: all 250ms;
}
.main-header__nav-item:hover,
.main-header__nav-item:focus {
  text-decoration: none;
}
.main-header__nav-item.active {
  color: #e22d22;
}
.main-header__nav-item.active .icon {
  fill: #e22d22;
  transform: scale(1.2);
}
.main-header__nav-item-text {
  padding-top: 3px;
  max-width: 0;
  max-height: 0;
  transition: max-width 500ms, max-height 500ms;
  overflow: hidden;
  white-space: nowrap;
}
.sin-card {
  position: relative;
  display: flex;
  flex-direction: column;
  border: 1px solid #e22d22;
  min-height: calc(100% - 15px);
  margin-bottom: 15px;
  padding: 50px 15px;
  background-color: #000;
}
.sin-card::before {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 100;
  content: '';
  opacity: 0;
  box-shadow: inset 0 0 3px 3px #e22d22;
  transition: opacity 250ms;
  pointer-events: none;
}
.sin-card:hover::before,
.sin-card:focus::before {
  opacity: 1;
}
.sin-card:hover .sin-card__delete,
.sin-card:focus .sin-card__delete {
  opacity: 1;
}
.sin-card__date {
  position: absolute;
  top: 10px;
  left: -2px;
  z-index: 100;
  display: flex;
  flex-direction: column;
  padding: 5px;
  background-color: #333;
  font-size: 12px;
  color: #fff;
}
.sin-card__delete {
  position: absolute;
  top: 5px;
  right: 5px;
  background: none;
  outline: none;
  border: none;
  transition: all 250ms;
  opacity: 0;
  font-size: 18px;
  transform: scale(1);
}
.sin-card__delete:hover,
.sin-card__delete:focus {
  transform: scale(1.4);
}
.sin-card__title {
  font-size: 16px;
  text-transform: uppercase;
}
.sin-card__additional {
  font-size: 14px;
}
